

import { AnimationEvent } from '@angular/animations';
import { Direction, Directionality } from '@angular/cdk/bidi';
import { BooleanInput } from '@angular/cdk/coercion';
import {
  AfterContentInit,
  AfterViewInit,
  ChangeDetectionStrategy,
  ChangeDetectorRef,
  Component,
  ContentChildren,
  ElementRef,
  EventEmitter,
  Host,
  Input,
  NgZone,
  OnDestroy,
  OnInit,
  Optional,
  Output,
  QueryList,
  TemplateRef,
  ViewChild,
  ViewChildren,
  ViewEncapsulation
} from '@angular/core';
import { defer, merge, Observable, Subject, Subscription } from 'rxjs';
import { filter, switchMap, take, takeUntil } from 'rxjs/operators';
import { slideMotion } from 'tds-ui/core/animation';
import { TDSNoAnimationDirective } from 'tds-ui/core/no-animation';
import { CompareWith } from 'tds-ui/core/util';
import { InputBoolean, TDSSafeAny } from 'tds-ui/shared/utility';


import { TDSAutocompleteOptionComponent, TDSAutoOptionSelectionChange } from './autocomplete-option.component';

export interface AutocompleteDataSourceItem {
  value: string;
  label: string;
}

export type AutocompleteDataSource = Array<AutocompleteDataSourceItem | string | number>;

@Component({
  selector: 'tds-autocomplete',
  exportAs: 'tdsAutocomplete',
  preserveWhitespaces: false,
  changeDetection: ChangeDetectionStrategy.OnPush,
  encapsulation: ViewEncapsulation.None,
  template: `
    <ng-template>
      <div
        #panel
        class="tds-select-dropdown tds-select-dropdown-placement-bottomLeft w-full bg-white py-1.5 shadow-1-lg rounded dark:bg-d-neutral-3-200"
        [class.tds-select-dropdown-hidden]="!showPanel"
        [class.hidden]="!showPanel"
        [class.tds-select-dropdown-rtl]="dir === 'rtl'"
        [ngClass]="overlayClassName"
        [ngStyle]="overlayStyle"
        @slideMotion
        (@slideMotion.done)="onAnimationEvent($event)"
        [@.disabled]="noAnimation?.noAnimation"
      >
        <div style="max-height: 256px; overflow-y: auto; overflow-anchor: none;" class="tds-custom-scroll">
          <div class="flex flex-col">
            <ng-template *ngTemplateOutlet="dataSource ? optionsTemplate : contentTemplate"></ng-template>
          </div>
        </div>
      </div>
      <ng-template #contentTemplate>
        <ng-content></ng-content>
      </ng-template>
      <ng-template #optionsTemplate>
        <tds-auto-option
          *ngFor="let option of dataSource!"
          [value]="option"
          [label]="option && $any(option).label ? $any(option).label : $any(option)"
        >
          {{ option && $any(option).label ? $any(option).label : $any(option) }}
        </tds-auto-option>
      </ng-template>
    </ng-template>
  `,
  animations: [slideMotion]
})
export class TDSAutocompleteComponent implements  AfterContentInit, AfterViewInit, OnDestroy, OnInit {
  static ngAcceptInputType_defaultActiveFirstOption: BooleanInput;
  static ngAcceptInputType_backfill: BooleanInput;

  @Input() width?: number;
  @Input() overlayClassName = '';
  @Input() overlayStyle: { [key: string]: string } = {};
  @Input() @InputBoolean() defaultActiveFirstOption = true;
  @Input() @InputBoolean() backfill = false;
  @Input() compareWith: CompareWith = (o1, o2) => o1 === o2;
  @Input() dataSource?: AutocompleteDataSource;
  @Input() autoClose:boolean = true;
  @Output()
  readonly selectionChange: EventEmitter<TDSAutocompleteOptionComponent> = new EventEmitter<TDSAutocompleteOptionComponent>();

  showPanel: boolean = true;
  isOpen: boolean = false;
  activeItem: TDSAutocompleteOptionComponent | null = null;
  dir: Direction = 'ltr';
  private destroy$ = new Subject<void>();
  animationStateChange = new EventEmitter<AnimationEvent>();

  /**
   * Options accessor, its source may be content or dataSource
   */
  get options(): QueryList<TDSAutocompleteOptionComponent> {
    // first dataSource
    if (this.dataSource) {
      return this.fromDataSourceOptions;
    } else {
      return this.fromContentOptions;
    }
  }

  /** Provided by content */
  @ContentChildren(TDSAutocompleteOptionComponent, { descendants: true })
  fromContentOptions!: QueryList<TDSAutocompleteOptionComponent>;
  /** Provided by dataSource */
  @ViewChildren(TDSAutocompleteOptionComponent) fromDataSourceOptions!: QueryList<TDSAutocompleteOptionComponent>;

  /** cdk-overlay */
  @ViewChild(TemplateRef, { static: false }) template?: TemplateRef<{}>;
  @ViewChild('panel', { static: false }) panel?: ElementRef;
  @ViewChild('content', { static: false }) content?: ElementRef;

  private activeItemIndex: number = -1;
  private selectionChangeSubscription = Subscription.EMPTY;
  private optionMouseEnterSubscription = Subscription.EMPTY;
  private dataSourceChangeSubscription = Subscription.EMPTY;
  /** Options changes listener */
  readonly optionSelectionChanges: Observable<TDSAutoOptionSelectionChange> = defer(() => {
    if (this.options) {
      return merge<TDSAutoOptionSelectionChange>(...this.options.map(option => option.selectionChange));
    }
    return this.ngZone.onStable.asObservable().pipe(
      take(1),
      switchMap(() => this.optionSelectionChanges)
    );
  });
  readonly optionMouseEnter: Observable<TDSAutocompleteOptionComponent> = defer(() => {
    if (this.options) {
      return merge<TDSAutocompleteOptionComponent>(...this.options.map(option => option.mouseEntered));
    }
    return this.ngZone.onStable.asObservable().pipe(
      take(1),
      switchMap(() => this.optionMouseEnter)
    );
  });

  constructor(
    private changeDetectorRef: ChangeDetectorRef,
    private ngZone: NgZone,
    @Optional() private directionality: Directionality,
    @Host() @Optional() public noAnimation?: TDSNoAnimationDirective
  ) {}
  ngOnInit(): void {
    this.directionality.change?.pipe(takeUntil(this.destroy$)).subscribe((direction: Direction) => {
      this.dir = direction;
      this.changeDetectorRef.detectChanges();
    });

    this.dir = this.directionality.value;
  }

  onAnimationEvent(event: AnimationEvent): void {
    this.animationStateChange.emit(event);
  }

  ngAfterContentInit(): void {
    if (!this.dataSource) {
      this.optionsInit();
    }
  }

  ngAfterViewInit(): void {
    if (this.dataSource) {
      this.optionsInit();
    }
  }

  ngOnDestroy(): void {
    this.dataSourceChangeSubscription.unsubscribe();
    this.selectionChangeSubscription.unsubscribe();
    this.optionMouseEnterSubscription.unsubscribe();
    this.destroy$.next();
    this.destroy$.complete();
  }

  setVisibility(): void {
    this.showPanel = !!this.options.length;
    this.changeDetectorRef.markForCheck();
  }

  setActiveItem(index: number): void {
    const activeItem = this.options.get(index);
    if (activeItem && !activeItem.active) {
      this.activeItem = activeItem;
      this.activeItemIndex = index;
      this.clearSelectedOptions(this.activeItem);
      this.activeItem.setActiveStyles();
    } else {
      this.activeItem = null;
      this.activeItemIndex = -1;
      this.clearSelectedOptions();
    }
    this.changeDetectorRef.markForCheck();
  }

  setNextItemActive(): void {
    const nextIndex = this.activeItemIndex + 1 <= this.options.length - 1 ? this.activeItemIndex + 1 : 0;
    this.setActiveItem(nextIndex);
  }

  setPreviousItemActive(): void {
    const previousIndex = this.activeItemIndex - 1 < 0 ? this.options.length - 1 : this.activeItemIndex - 1;
    this.setActiveItem(previousIndex);
  }

  getOptionIndex(value: TDSSafeAny): number {
    return this.options.reduce(
      (result: number, current: TDSAutocompleteOptionComponent, index: number) =>
        result === -1 ? (this.compareWith(value, current.value) ? index : -1) : result,
      -1
    )!;
  }

  getOption(value: TDSSafeAny): TDSAutocompleteOptionComponent | null {
    return this.options.find(item => this.compareWith(value, item.value)) || null;
  }

  private optionsInit(): void {
    this.setVisibility();
    this.subscribeOptionChanges();
    const changes = this.dataSource ? this.fromDataSourceOptions.changes : this.fromContentOptions.changes;
    // async
    this.dataSourceChangeSubscription = changes.subscribe(e => {
      if (!e.dirty && this.isOpen) {
        setTimeout(() => this.setVisibility());
      }
      this.subscribeOptionChanges();
    });
  }

  /**
   * Clear the status of options
   */
  clearSelectedOptions(skip?: TDSAutocompleteOptionComponent | null, deselect: boolean = false): void {
    this.options.forEach(option => {
      if (option !== skip) {
        if (deselect) {
          option.deselect();
        }
        option.setInactiveStyles();
      }
    });
  }

  private subscribeOptionChanges(): void {
    this.selectionChangeSubscription.unsubscribe();
    this.selectionChangeSubscription = this.optionSelectionChanges
      .pipe(filter((event: TDSAutoOptionSelectionChange) => event.isUserInput))
      .subscribe((event: TDSAutoOptionSelectionChange) => {
        event.source.select();
        event.source.setActiveStyles();
        this.activeItem = event.source;
        this.activeItemIndex = this.getOptionIndex(this.activeItem.value);
        this.clearSelectedOptions(event.source, true);
        this.selectionChange.emit(event.source);
      });

    this.optionMouseEnterSubscription.unsubscribe();
    this.optionMouseEnterSubscription = this.optionMouseEnter.subscribe((event: TDSAutocompleteOptionComponent) => {
      event.setActiveStyles();
      this.activeItem = event;
      this.activeItemIndex = this.getOptionIndex(this.activeItem.value);
      this.clearSelectedOptions(event);
    });
  }
}
