

import { BooleanInput } from '@angular/cdk/coercion';
import {
  ChangeDetectionStrategy,
  ChangeDetectorRef,
  Component,
  ElementRef,
  EventEmitter,
  Input,
  Optional,
  Output,
  ViewEncapsulation
} from '@angular/core';
import { InputBoolean, TDSSafeAny } from 'tds-ui/shared/utility';
import { scrollIntoView } from 'tds-ui/core/util';


import { TDSAutocompleteOptgroupComponent } from './autocomplete-optgroup.component';

export class TDSAutoOptionSelectionChange {
  constructor(public source: TDSAutocompleteOptionComponent, public isUserInput: boolean = false) { }
}

@Component({
  selector: 'tds-auto-option',
  exportAs: 'tdsAutoOption',
  // preserveWhitespaces: false,
  changeDetection: ChangeDetectionStrategy.OnPush,
  encapsulation: ViewEncapsulation.None,
  template: `
    <div class="tds-select-item-option-content w-full flex break-words" [ngClass]="{'py-2': size === 'md',
    'py-1.5': size === 'sm',
    'py-2.5': size === 'lg'}">    
      <ng-content></ng-content>
    </div>
  `,
  host: {
    role: 'menuitem',
    class: 'tds-select-item tds-select-item-option px-3  text-neutral-1-900 dark:text-d-neutral-1-900 text-body-2 font-regular hover:bg-neutral-3-50 dark:hover:bg-d-neutral-3-300 flex w-full',
    '[class.tds-select-item-option-grouped]': 'TDSAutocompleteOptgroupComponent',
    '[class.tds-select-item-option-selected]': 'selected',
    '[class.bg-neutral-3-50]': '(selected ||active) && !disabled',
    '[class.dark:bg-d-neutral-3-300]': '(selected ||active) && !disabled',
    '[class.tds-select-item-option-active]': 'active',    
    '[class.cursor-pointer]':"!disabled",
    '[class.cursor-not-allowed]':"disabled",
    '[class.tds-select-item-option-disabled]': 'disabled',
    '[attr.aria-selected]': 'selected.toString()',
    '[attr.aria-disabled]': 'disabled.toString()',
    '(click)': 'selectViaInteraction()',
    '(mouseenter)': 'onMouseEnter()',
    '(mousedown)': '$event.preventDefault()'
  }
})
export class TDSAutocompleteOptionComponent {
  static ngAcceptInputType_disabled: BooleanInput;
  @Input() size: 'sm' | 'md' | 'lg' = 'md';
  @Input() value: TDSSafeAny;
  @Input() label?: string;
  @Input() @InputBoolean() disabled = false;
  @Output() readonly selectionChange = new EventEmitter<TDSAutoOptionSelectionChange>();
  @Output() readonly mouseEntered = new EventEmitter<TDSAutocompleteOptionComponent>();

  active = false;
  selected = false;

  constructor(
    private changeDetectorRef: ChangeDetectorRef,
    private element: ElementRef,
    @Optional()
    public TDSAutocompleteOptgroupComponent: TDSAutocompleteOptgroupComponent
  ) { }

  select(emit: boolean = true): void {
    this.selected = true;
    this.changeDetectorRef.markForCheck();
    if (emit) {
      this.emitSelectionChangeEvent();
    }
  }

  onMouseEnter(): void {
    this.mouseEntered.emit(this);
  }

  deselect(): void {
    this.selected = false;
    this.changeDetectorRef.markForCheck();
    this.emitSelectionChangeEvent();
  }

  /** Git display label */
  getLabel(): string {
    return this.label || this.value.toString();
  }

  /** Set active (only styles) */
  setActiveStyles(): void {
    if (!this.active) {
      this.active = true;
      this.changeDetectorRef.markForCheck();
    }
  }

  /** Unset active (only styles) */
  setInactiveStyles(): void {
    if (this.active) {
      this.active = false;
      this.changeDetectorRef.markForCheck();
    }
  }

  scrollIntoViewIfNeeded(): void {
    scrollIntoView(this.element.nativeElement);
  }

  selectViaInteraction(): void {
    if (!this.disabled) {
      this.selected = !this.selected;
      if (this.selected) {
        this.setActiveStyles();
      } else {
        this.setInactiveStyles();
      }
      this.emitSelectionChangeEvent(true);
      this.changeDetectorRef.markForCheck();
    }
  }

  private emitSelectionChangeEvent(isUserInput: boolean = false): void {
    this.selectionChange.emit(new TDSAutoOptionSelectionChange(this, isUserInput));
  }
}
